		getAdminTeamTable: function getAdminTeamTable(model) {
			var maxRev = model.list[0].score.revenue;
			var html = "\n\t\t\t\t<table class=\"is-fullwidth\">\n\t\t\t\t\t<thead>\n\t\t\t\t\t\t<tr>\n\t\t\t\t\t\t\t<th>Rank</th>\n\t\t\t\t\t\t\t<th>Team</th>\n\t\t\t\t\t\t\t<th>Revenue</th>\n\t\t\t\t\t\t\t<th>Trips</th>\n\t\t\t\t\t\t\t<th>View</th>\n\t\t\t\t\t\t</tr>\n\t\t\t\t\t</thead>\n\t\t\t\t\t<tbody>\n\t\t\t";
			model.list.forEach(function (team) {
				var revRatio = team.score.revenue / maxRev;
				var opac = 0.5 * revRatio + 0.5;
				var styleStr = "background-color: rgba(152, 244, 66, " + opac.toFixed(3) + ")";
				var spanHTML = '';
				if (team.rank === 1) {
					spanHTML = "\n\t\t\t\t\t\t<span class=\"icon\">\n\t\t\t\t\t\t\t<i class=\"icon fa fa-trophy\"></i>\n\t\t\t\t\t\t</span>\n\t\t\t\t\t";
				}
				html += "\n\t\t\t\t\t\t<tr>\n\t\t\t\t\t\t\t<td><span>" + team.rank + "</span>" + spanHTML + "</td>\n\t\t\t\t\t\t\t<td>" + team.info.name + "</td>\n\t\t\t\t\t\t\t<td style=\"" + styleStr + "\">$" + team.score.revenue.formatMoney(2) + "</td>\n\t\t\t\t\t\t\t<td>" + team.score.trips + "</td>\n\t\t\t\t\t\t\t<td><a href=\"/#/team/" + team.info.teamid + "\" class=\"button is-primary is-outlined\">View Team</a></td>\n\t\t\t\t\t\t</tr>\n\t\t\t\t";
			});
			html += "\n\t\t\t\t\t</tbody>\n\t\t\t\t</table>\n\t\t\t";
			var div = document.createElement('div');
			div.innerHTML = html;
			div.classList.add('table-holder');
			return div;
		},

